#page-content {
    padding: 0; }

#motto {
    float: left;
    padding: 30px;
    width: 100%;
    font-size: 1.25em;
    text-align: center; }

#motto:first-letter {
    text-transform: uppercase; }

#column-left-flex {
    display: flex;
    flex-direction: column;
    float: left; }

#welcome {
    float: left; }

#wallets {
    float: left;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%; }

#wallets a {
    display: flex;
    flex: 1 0 50%;
    padding: 10px;
    margin-bottom: 10px;
    font-size: 0.9em;
    text-decoration: none;
    background-color: #F8F8FB;
    border: 1px solid #E5E5E5;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px; }

#wallets a > div {
    padding: 10px;
    margin-top: auto;
    margin-bottom: auto;
    font-size: 0.95em; }

#wallets a > div > span {
    padding-left: 10px; }

#wallets a:hover {
    background-color: #F3F3F8;
    color: #111;
    border: 1px solid #888; }

.articles-list {
    padding: 10px 0px;
    width: 100%;
    border: 1px solid #E5E5E5;
    border-radius: 4px;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px; }

.articles-list a {
    text-decoration: none;
    border: none; }

.articles-list a:hover .article-img img {
    opacity: 1; }

.articles-list a:hover .article {
    color: #000; }

.article {
    float: left;
    padding: 15px 20px;
    width: 100%;
    border: none;
    border-radius: 4px;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px; }

.article-img {
    float: left;
    width: 300px; }

.article-img img {
    display: block;
    max-width: 100%;
    opacity: 0.9; }

.article-txt {
    padding: 5px 20px 20px 20px;
    margin-left: 300px;
    line-height: 1.45em; }

.article-title {
    font-weight: bold;
    font-size: 1em; }

.article-description {
    padding-top: 15px;
    font-size: 0.9em;
    color: #444; }

#history {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row; }

#history a {
    display: flex;
    flex: 1 0 100%;
    margin-bottom: 10px;
    font-size: 0.9em;
    text-decoration: none;
    background-color: #F8F8FB;
    border: 1px solid #E5E5E5;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px; }

#history a:hover {
    background-color: #F3F3F8;
    color: #111;
    border: 1px solid #888; }

#history img {
    min-width: 100px;
    max-width: 100px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    -moz-border-radius-topright: 0;
    -moz-border-radius-bottomright: 0;
    -webkit-border-top-right-radius: 0;
    -webkit-border-bottom-right-radius: 0; }

#history div {
    padding: 10px;
    margin-top: auto;
    margin-bottom: auto;
    font-size: 0.95em; }

#social-media {
    float: left;
    width: 100%; }

#social-media a {
    text-decoration: none; }

#social-media i {
    margin-right: 7px;
    font-size: 2em; }

@media screen and (max-width: 1150px) {
    #column-left,
    #column-right {
        width: 100%; } }

@media screen and (max-width: 700px) {
    #motto {
        padding: 20px 0 25px; }

    #wallets a {
        flex: 1 0 100%; }

    .article-txt {
        padding: 15px 5px 5px 5px; }

    .article-img,
    .article-txt {
        float: left;
        margin: 0;
        width: 100%; }

    .article-img img {
        width: 100%; } }
